import React, { useContext } from 'react'
import classes from './styles.css'
import { Context } from '../../../../context/MenuContext'

const Toggle = () => {
    const { toggleMenuMobile } = useContext(Context)

    return (
        <button type='button' className={classes.Toggle} onClick={() => toggleMenuMobile()}>
            <span />
            <span />
            <span />
        </button>
    )
}

export default Toggle