import React from 'react'
import { FaHtml5, FaCss3Alt, FaJs, FaReact, FaNode } from 'react-icons/fa'
import classes from './styles.css'

import Grid from '../UI/Grid'
import Title from '../UI/Title'
import Skill from './Skill'

const skills = [
    { Icon: FaHtml5, title: 'HTML5'}, 
    { Icon: FaCss3Alt, title: 'CSS3'}, 
    { Icon: FaJs, title: 'Javascript'}, 
    { Icon: FaReact, title: 'React' }, 
    { Icon: FaReact, title: 'React Native' }, 
    { Icon: FaNode, title: 'Node.js' }
]

const Skills = () => {
    return (
        <section id='skills' className={classes.Skills}>
            <div className={classes.container}>
                <Title>Minhas <b>habilidades</b></Title>
                <Grid container>
                    { skills.map((skill, index) => <Skill key={index} data={skill} />) }
                </Grid>
            </div>
        </section>
    )
}

export default Skills