import React, { useContext } from 'react'
import classes from './styles.css'

import Item from './Item'
import { Context } from '../../../../context/MenuContext'

const menuItems = [
    { targetId: 'home', label: 'Home' }, 
    { targetId: 'about', label: 'Sobre' }, 
    { targetId: 'skills', label: 'Habilidades' }, 
    { targetId: 'contact', label: 'Contato' }
]

const Items = () => {
    const classList = [classes.Items]

    const { showMenuMobile } = useContext(Context)

    if(showMenuMobile) {
        classList.push(classes.ShowMenuMobile)
    }

    return (
        <ul className={classList.join(' ')}>
            {
                menuItems.map((menuItem, index) => {
                    return (
                        <Item 
                            key={index} 
                            targetId={menuItem.targetId} 
                            label={menuItem.label} 
                        />
                    )
                })
            }
        </ul>
    )
}

export default Items