import React from 'react'
import classes from './styles.css'

const Container = (props) => (
    <div className={classes.Container} style={{...props.style}}>
        {props.children}
    </div>
)

const Col = (props) => {
    const classList = [classes.Col]

    if(props.col && (props.col >= 1 && props.col <= 12)) {
        const col = Math.floor(props.col)
        classList.push(classes[`Col${col}`])
    }

    return (
        <div className={classList.join(' ')} style={{...props.style}}>
            {props.children}
        </div>
    )
}

const Grid = (props) => {
    if(props.container) {
        return <Container {...props} />
    } else {
        return <Col {...props} />
    }
}

export default Grid