import * as actionTypes from '../actions/actionsTypes'

export const initialState = {
    loading: false,
    success: false,
    error: false
}

const sendContactStart = () => {
    return { loading: true, success: false, error: false }
}

const sendContactSuccess = () => {
    return { loading: false, success: true, error: false }
}

const sendContactFail = () => {
    return { loading: false, success: false, error: true }
}

const reducer = (state = initialState, action) => {
    switch(action.type) {
        case actionTypes.SEND_CONTACT_START: return sendContactStart()
        case actionTypes.SEND_CONTACT_SUCCESS: return sendContactSuccess()
        case actionTypes.SEND_CONTACT_FAIL: return sendContactFail()
        default: return state
    }
}

export default reducer