import React, { useState, useReducer } from 'react'
import classes from './styles.css'

import { FaEnvelope, FaLinkedin, FaGithub } from 'react-icons/fa'
import Title from '../UI/Title'
import Grid from '../UI/Grid'
import Paper from '../UI/Paper'
import Input from '../UI/Input'
import Textarea from '../UI/Textarea'
import Button from '../UI/Button'
import Loading from '../UI/Loading'
import Success from '../UI/Success'
import Error from '../UI/Error'
import sendContactReducer, { initialState } from '../../store/reducers/sendContact'
import sendContact from '../../store/actions/sendContact'

const isEmpty = value => {
    const val = value.trim()
    
    if(val === '' || val === null || val === undefined) return true

    return false
}

const Contact = () => {
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [subject, setSubject] = useState('')
    const [message, setMessage] = useState('')
    const [error, setError] = useState('')

    const [state, dispatch] = useReducer(sendContactReducer, initialState)

    const handleSubmit = (e) => {
        e.preventDefault()
        
        if(isEmpty(name)) return setError('Digite o nome')
        if(isEmpty(email)) return setError('Digite o e-mail')
        if(isEmpty(subject)) return setError('Digite o assunto')
        if(isEmpty(message)) return setError('Digite a mensagem')

        const data = { name, email, subject, message }

        sendContact(data, dispatch)
    }

    const renderContactForm = () => {
        if(state.loading) {
            return <Loading label='Enviando...' />
        } else if(state.success) {
            return <Success label='Formulário enviado com sucesso.' />
        } else if(state.error) {
            return <Error label={`Erro ao enviar formuário. \nTente novamente mais tarde.`} />
        } else {
            return (
                <form method='post' onSubmit={handleSubmit}>
                    { error && <div className={classes.Error}>{error}</div> }
                    <Grid container>
                        <Grid col={6}>
                            <Input 
                                type='text' 
                                placeholder='Nome' 
                                value={name} 
                                onChange={e => setName(e.target.value)} 
                                required
                            />
                        </Grid>
                        <Grid col={6}>
                            <Input 
                                type='email' 
                                placeholder='E-mail' 
                                value={email} 
                                onChange={e => setEmail(e.target.value)} 
                                required
                            />
                        </Grid>
                        <Grid col={12} style={{ flex: '100% 0 0' }}>
                            <Input 
                                type='text' 
                                placeholder='Assunto' 
                                value={subject} 
                                onChange={e => setSubject(e.target.value)} 
                                required
                            />
                        </Grid>
                        <Grid col={12} style={{ flex: '100% 0 0' }}>
                            <Textarea 
                                rows={10} 
                                placeholder='Mensagem' 
                                value={message} 
                                onChange={e => setMessage(e.target.value)} 
                                required
                            />
                        </Grid>
                        <Grid col={12}>
                            <Button type='submit'>Enviar</Button>
                        </Grid>
                    </Grid>
                </form>
            )
        }
    }

    return (
        <section id="contact" className={classes.Contact}>
            <div className={classes.container}>
                <Title>Entre em <b>contato</b></Title>
                <Grid container>
                    <Grid col={7}>
                        <Paper style={{ margin: '10px 0' }}>
                            {renderContactForm()}
                        </Paper>
                    </Grid>
                    <Grid col={5}>
                        <Paper style={{ margin: '10px 0' }}>
                            <div className={classes.EmailInfos}>
                                <i className={classes.Icon}>
                                    <FaEnvelope />
                                </i>
                                <div>
                                    <b style={{ display: 'block' }}>E-mail: </b>
                                    diogomedina97@hotmail.com
                                </div>
                            </div>
                        </Paper>
                        <Paper style={{ margin: '10px 0' }}>
                            <div className={classes.SocialNetworkInfos}>
                                <a href='https://www.linkedin.com/in/diogomedina97/' target='_blank' rel='noopener noreferrer' className={classes.Icon}>
                                    <FaLinkedin />
                                </a>
                                <a href='https://github.com/DiogoMedina97' target='_blank' rel='noopener noreferrer' className={classes.Icon}>
                                    <FaGithub />
                                </a>
                            </div>
                        </Paper>
                    </Grid>
                </Grid>
            </div>
        </section>
    )
}

export default Contact