import * as actionTypes from './actionsTypes'
import axios from 'axios'

const sendContactStart = () => {
    return {
        type: actionTypes.SEND_CONTACT_START
    }
}

const sendContactSuccess = () => {
    return {
        type: actionTypes.SEND_CONTACT_SUCCESS
    }
}

const sendContactFail = () => {
    return {
        type: actionTypes.SEND_CONTACT_FAIL
    }
}

const sendContact = (data, dispatch) => {
    dispatch(sendContactStart())
    setTimeout(() => {
        axios.post(process.env.REACT_APP_FORMSPREE_URL, data)
            .then(success => {
                dispatch(sendContactSuccess())
            })
            .catch(error => {
                dispatch(sendContactFail())
            })
    }, 2000)
}

export default sendContact