import React from 'react'
import classes from './styles.css'

import Logo from './Logo'
import Items from './Items'
import Toggle from './Toggle'

const Menu = () => {
    return (
        <nav className={classes.Menu}>
            <div className={classes.container}>
                <Logo />
                <Items />
                <Toggle />
            </div>
        </nav>
    )
}

export default Menu